import { Box, Typography } from "@rp/react/web/mui/core"

type Props = {
  text?: string
  filterTerm?: string
}

export const HubNoResults = ({ text, filterTerm = "" }: Props) => {
  const label =
    filterTerm.length > 0 ? "No results found for your search." : "You currently have no items here"
  return (
    <Box sx={{ mt: 3, ml: 0.5 }}>
      <Typography sx={{ color: "red" }} data-testid="hub-no-results-subtitle">
        {text ?? label}
      </Typography>
    </Box>
  )
}
